<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process222"></a>

<p><br></p>
<p><span class="rvts17">痛苦的解药（</span><span class="rvts50">Bitter Tonic</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:506,Y:450</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：黑寡妇制造者（</span><span class="rvts20">Black Widowmaker</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">为黑寡妇制造者复仇</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1286.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">与锯木厂中心位置的黑寡妇制造者谈话，她会告诉你她曾是孤狼的一名囚犯，然而她身旁那个叫做蛇根（</span><span class="rvts20">Snakeroot</span><span class="rvts21">）的孤狼蜥蜴，曾在她生前把它给毒死了。</span>
<br><span class="rvts21">杀掉蛇根，为黑寡妇制造者复仇。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



